import moment from "moment";
import postStyle from "./news.scss";

const News = (props) => {
  return (
    <div className={postStyle.post}>
      {props.post.photo ? (
        <div className={postStyle.img}>
          <img src={props.post.photo} alt={"titleImg"} />
        </div>
      ) : undefined}
      <h2 className={postStyle.title}>{props.post.title}</h2>
      <p className={postStyle.text}>{props.post.text}</p>
      <div className={postStyle.tag}>
        <span className={postStyle.category}>{props.post.category}</span>
        <span className={postStyle.date}>
          {moment(props.post.date).locale("uk").fromNow()}
        </span>
      </div>
    </div>
  );
};

export default News;
